
    html, body {
        padding: 0;
        margin: 0;
    }

    .target {
        height: 30px;
        width: 120px;
        background: red;
        position: absolute;
        box-shadow: 0 2px 5px rgba(0,0,0,1);
        will-change: transform;
    }

    .shooter {
        height: 30px;
        width: 150px;
        position: absolute;
        perspective: 700px;
        will-change: transform;
    }

    .shipBody {
        height: 100%;
        width: 100%;
        background: green;
        box-shadow: 0 2px 5px rgba(0,0,0,1);
        transition: transform 0.3s;
        z-index: 1;
    }

    .cannon {
        background-color: greenyellow;
        width: 20px;
        height: 60px;
        position: absolute;
        bottom: 0;
        left: 65px;
    }

    .projectile {
        height: 30px;
        width: 8px;
        position: absolute;
        will-change: transform;
    }

    .projectile.enemyProj {
        background: red;
        border-bottom-right-radius: 40%;
        border-bottom-left-radius: 40%;
    }

    .projectile.allyProj {
        background: blue;
        border-top-right-radius: 40%;
        border-top-left-radius: 40%;
    }

    .powerUp {
        background-color: bisque;
        height: 50px;
        width: 50px;
        border-radius: 50%;
        position: absolute;
        will-change: transform;
        vertical-align: middle;
        text-align: center;
        line-height: 50px;
    }
